body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
        'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
    box-sizing: border-box;
}

#root {
    display: flex;
    flex-direction: column;

    font-family: 'Roboto Mono', monospace;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.2;
    color: #160901;
}

.blue-bg {
    background-color: #3b85ff;
    color: #fff;
}

.container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0px 30px;
}
.content {
    max-width: 1400px;
    width: 100%;
}
.flex-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

h1 {
    font-weight: 700;
    font-size: 36px;
    margin: 0px;
    margin-bottom: 30px;
}
h2 {
    font-weight: 500;
    font-size: 24px;
    margin: 0px;
}
h3 {
    margin-top: 0px;
    font-size: 24px;
    font-weight: 700;
}
p {
    margin: 0px;
}
.spacer-30 {
    padding-top: 30px;
}

.lightblue-p {
    font-weight: 400;
    font-size: 20px;
    color: #cffffc;
}

.menu {
    min-height: 86px;
    position: fixed;
    z-index: 100;
}
.logo-box {
    display: flex;
    align-items: center;
}
.logo-box img {
    margin-right: 15px;
    height: 33px;
    width: 30px;
}

.nav {
    display: flex;
}
.nav .nav-item {
    color: #fff;
    padding: 10px;
    margin: 0 20px;
    cursor: pointer;
}
.nav .nav-item:first-child {
    padding-left: 0px;
    margin-left: 0px;
}
.nav .nav-item:last-child {
    padding-right: 0px;
    margin-right: 0px;
}
.nav .nav-item:hover {
    color: #fff;
    opacity: 0.6;
}

.header {
    padding-top: 142px;
    padding-bottom: 76px;
}
.box {
    width: calc(50% - 30px);
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding-top: 50px;
}
.full-width {
    width: 100%;
}

.unmargin-pre pre {
    margin: 0;
}

.spacer {
    padding-top: 30px;
}

.text-box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.lib-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    align-self: flex-end;
}
.install-box {
    background-color: #1c1c1c;
    border-left: 6px solid #fff;
    position: relative;
    cursor: pointer;
}
.install-box input {
    background-color: transparent;
    padding: 14px 30px;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 500;
    color: #fff;
    width: 330px;
    border: none;
    outline: none;
    cursor: pointer;
}
.install-box .copy-success {
    position: absolute;
    right: -75px;
    top: 15px;
    font-size: 15px;
    font-weight: 500;
}
.git-box {
    padding: 14px 0px;
    display: flex;
    align-items: center;
    font-weight: 500;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
}
.git-box img {
    margin-right: 15px;
}
.git-box:hover {
    opacity: 0.6;
}

.sheet-box {
    position: relative;
    background-color: #fff;
    height: 330px;
    width: 100%;
    color: #160901;
    flex-grow: 1;
}

.title-separator {
    display: flex;
    align-items: center;
    width: 100%;
    padding-left: calc((100% - 1460px) / 2);
    padding-top: 50px;
}
.title-separator p {
    flex-grow: 0;
    padding: 0px 30px 5px 30px;
    color: #3b85ff;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    margin: 0;
}
.title-separator .separator {
    height: 3px;
    background-color: #3b85ff;
    flex-grow: 1;
}

.code-box {
    border: 1px solid #3b85ff;
    padding: 30px;
    width: 100%;
    overflow-x: auto;
}
.code-space {
    width: 100%;
    height: 16px;
}
.code-line {
    display: flex;
    flex-wrap: nowrap;
}
.code-line .red {
    color: #a81011;
}
.code-line .orange {
    color: #ea7a13;
}
.code-line .violet {
    color: #1713a3;
}
.code-line .blue {
    color: #0068a3;
}
.code-line .green {
    color: #11a32e;
}
.code-line .padd-10 {
    padding-right: 10px;
}
.code-line.padd-20 {
    padding-left: 20px;
}
.code-line.padd-40 {
    padding-left: 40px;
}
.unpadd {
    padding-top: 0px;
}

span.emphased {
    background-color: #3b85ff33;
    padding: 0px 5px;
}

.footer {
    margin-top: 60px;
    padding: 50px 0px;
    background-color: #3b85ff;
    display: flex;
    align-items: center;
    justify-content: center;
}
.footer p.copy {
    color: #fff;
    font-weight: 700;
    margin: 0px;
}

@media only screen and (max-width: 1399px) {
    .lib-box {
        flex-direction: column;
        align-items: flex-start;
        padding-top: 40px;
    }
}

@media only screen and (max-width: 991px) {
    .content {
        flex-wrap: wrap;
    }
    .box {
        width: 100%;
        height: auto;
    }
    .menu .logo-box {
        width: 100%;
        padding-top: 20px;
        padding-bottom: 15px;
    }
    .nav {
        padding-bottom: 20px;
    }
}

@media only screen and (max-width: 600px) {
    .nav .nav-item {
        margin: 0px;
        padding: 0 7px;
        font-size: 15px;
    }
    .install-box input {
        padding: 14px 20px;
        width: 310px;
    }
    .sheet-box {
        min-height: 200px;
    }
    .sheet-box div {
        height: 100%;
    }
}
